/*
    RESPONSIVE TABS by Oliver Knoblich
    https://codepen.io/oknoblich/pen/tfjFl
*/

.tabla-record {
    display: none;
    padding: 20px 0;
    border: 1px solid var(--impureGrey);
    text-align: center;
}

.tabla-grid {
    display: inline-grid;
    grid-template-columns: repeat(3, 33.33%);
    border: 1px solid #000;
    width: 90%;
    background-color: #fff;
}

.input-record {
    display: none;
}

.label-record {
    display: inline-block;
    margin: 0 0 -1px;
    padding: 2px;
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: var(--impureGrey);
    border: 1px solid var(--impureGrey);
    width: 20%;
    &:hover {
        color: var(--impureBlack);
        cursor: pointer;
    }
    &:nth-of-type(4n+1) {
        margin-left: 10px;
    }
}

.label-responsive {
    display: none;
}

.input-record:checked + .label-record {
    color: var(--impureBlack);
    border: 1px solid var(--impureGrey);
    border-top: 5px solid var(--gradient2);
    border-bottom: 1px solid var(--impureWhite);
    background-color: var(--impureWhite);
}

#tab1:checked ~ .contenido1,
#tab2:checked ~ .contenido2,
#tab3:checked ~ .contenido3,
#tab4:checked ~ .contenido4,
#tab5:checked ~ .contenido5,
#tab6:checked ~ .contenido6,
#tab7:checked ~ .contenido7,
#tab8:checked ~ .contenido8,
#tab9:checked ~ .contenido9,
#tab10:checked ~ .contenido10,
#tab11:checked ~ .contenido11,
#tab12:checked ~ .contenido12 {
    display: block;
    background-color: var(--impureWhite);
}

.grid-text,
.grid-text-bold {
    display: block;
    padding: 10px 0;
    font-family: var(--roboto);
    &:not(:last-child) {
        border-bottom: 1px solid #ccc;
    }
}

.grid-text-bold {
    font-weight: bold;
}

.grid-title {
    border-bottom: 1px solid #ccc;
    padding: 10px;
    margin: 0;
}

.grid-title-min {
    background-color: var(--btnMinBgc);
}

.grid-title-max {
    background-color: var(--gradient2);
}

.tabla-record-header {
    width: 100%;
    display: inline-block;
    font-size: 0;
}

.tabla-record-header-text {
    display: inline-block;
    font-size: 30px;
    margin-bottom: 10px;
    width: 90%;
    text-align: left;
}

.tabla-record-minimas-desktop {
    display: none;
}

.record-chart-maximas,
.record-chart-minimas {
    .xAxis {
        path {
            display: none;
        }
        .tick line {
            display: none;
        }
    }
}

.container-recomendaciones {
    font-size: 0;
    position: relative;
}

.imagen-recomendaciones {
    width: 100%;
}

.container-recomendaciones-lista {
    position: absolute;
    right: 5vw;
    top: -5vh;
    max-width: 25vw;
    background-color: #fafafa;
    border-radius: 5px;
    list-style-type: none;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, .2);
    padding-left: 0;
}

.container-recomendaciones-elemento {
    padding: 1rem;
    vertical-align: middle;
    &:not(:last-child) {
        border-bottom: 1px solid #E1E1E1;
    }
    &:first-child {
        padding-top: 1.5rem;
    }
    &:last-child {
        padding-bottom: 1.5rem;
    }
}

.container-recomendaciones-texto {
    display: inline-block;
    vertical-align: middle;
    width: calc(100% - 50px);
    font-size: 14px;
    font-weight: normal;
    font-family: var(--h1);
    padding-left: .5rem;
}

.icon-recomendaciones {
    width: 48px;
    height: 48px;
    display: inline-block;
    vertical-align: middle;
    path {
        fill: #72BE49;
        stroke: none;
    }
}

.recomendaciones-subtitle {
    color: var(--impureBlack);
    border-radius: 5px;
    font-family: var(--h1);
    font-size: 1rem;
    max-width: 40vw;
}
